<template>
  <div class="ao-person-jyrz">
    <div class="table">
      <div class="theader">
        <div class="th">
          <div class="td" v-for="(item, index) in jyzt_td" :key="index">{{ item }}</div>
        </div>
      </div>
      <div class="tbody">
        <div
          class="tr"
          v-for="(item, index) in list"
          :key="index"
        >
          <div class="td">{{ item.add_time }}</div>
          <div class="td">{{ item.message }}</div>
          <div class="td">{{ item.money }}</div>
        </div>
        <div class="more" @click="more">{{ moreFalg ? $t('person.more') : $t('person.no_more') }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { getDealLog } from '@/api/asset'

export default {
  name: "AOAssetReturn",
  data() {
    return {
      list: [],
      page: 1,
      moreFalg: false,
      jyzt_td: this.$t('person.jyzt_td')
    }
  },
  mounted() {
    this.getDealLog()
  },
  methods: {
    more() {
      if(this.moreFalg) return
      this.page ++
      this.getDealLog()
    },
    getDealLog() {
      getDealLog({ page: this.page }).then(res => {
        if (res.length > 0)
          this.list = this.list.concat(res)
        else
          this.moreFalg = true
      })
    }
  }
};
</script>

<style scoped lang="scss">
.ao-person-jyrz {
  margin-top: -34px;
  .tbody {
    max-height: 600px;
    overflow-y: auto;
  }
  .th {
    padding: 15px 0;
    border-bottom: 1px solid #303b4b;
    .td {
      font-size: 14px;
    }
  }
  .tr {
    padding: 15px 0;
    cursor: pointer;
  }
  .tr:hover {
    background: #61688a;
  }
  .th,
  .tr {
    display: flex;
  }
  .td {
    flex: 1;
    text-align: center;
    color: #fff;
    font-size: 12px;
  }
  .more {
    color: #999;
    font-size: 12px;
    margin-top: 10px;
    cursor: pointer;
    text-align: center;
  }
}
</style>



